.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 0px;
}
.hello {
  font-size: 32px;
  color: aliceblue;
  filter: contrast(80%);
}
.btn {
  font-family: "Helvetica", "Arial", sans-serif;
  float: right;
  font-size: 12px;
  padding: 7px;
  cursor: pointer;
  background-color: darkgrey;
  border: 0;
  padding: 8px 20px;

  text-transform: uppercase;
  cursor: pointer;
}
