.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}
.h1 {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 20px;
}
.input_field {
  width: 70%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 10px;
}

.add_button {
  font-family: "Helvetica", "Arial", sans-serif;
  float: right;
  font-size: 12px;
  padding: 7px;
  cursor: pointer;
  background-color: indianred;
  border: 0;
  padding: 8px 20px;

  text-transform: uppercase;
  cursor: pointer;
}
.add_button:hover {
  background-color: darkred;
}

.todo_list {
  list-style-type: none;
  padding: 20px 0 0 0;
}

.todo_item {
  padding-right: 20px;
  font-size: 20px;
  color: #ddd;
  align-items: center;
  margin-bottom: 10px;
}

.deletebutton {
  background-color: #ffffff00;
  color: white;
  border: none;
  padding: 8px 5px 5px 5px;
  font-size: 14px;
  border-radius: 10px;
  cursor: pointer;
  float: right;
}
